'use strict';

// MODULES //

import copy from '@stdlib/utils-copy';
import db from './db.js';


// MAIN //

/**
* Returns file data.
*
* @private
* @param {NonNegativeInteger} id - file id
* @returns {Object} file data
*/
function getFile( id ) {
	return copy( db[ id ] );
}


// EXPORTS //

export default getFile;
